import React from 'react'

function ProductTileImage(item) {

    const imageLink = require(`${item.imageUrl}`)
    console.log(`imageLink: ${imageLink}`)


    return <div className="aspect-w-4 aspect-h-4 bg-white rounded-3xl group-odd:shadow-kcyan4 shadow-kyellow4 isolate group-hover:shadow-kmag4 overflow-hidden" key={item.id}>
        <img
            src={imageLink}
            className="object-cover object-center w-full scale-110 "
            alt={item.imageAlt} />
    </div>
}

export default ProductTileImage